.section-1 {
  .sticky-box {
    > div {
      top: 0;
      transition: all 0.5s ease-in-out;
      height: calc(100vh - 350px);

      &.sticky {
        transform: translateY(100px);
      }

      .title {
        font-size: 3rem;
        color: #fff;
      }

      .sub {
        font-size: 1.5rem;
        color: #aaa;
      }
    }
  }

  .card-list {
    > li {
      width: 100%;
      margin-bottom: 10rem;
      display: flex;
      align-items: center;
      gap: 2rem;

      .text-box {
        width: 60%;

        .title {
          color: #fff;
          font-size: 2rem;
          font-weight: 500;
        }

        .desc {
          color: #fff;
          font-size: 1.2rem;
          margin-top: 1rem;
        }
      }

      .img-box {
        width: 40%;

        .img-overflow {
          width: 100%;
          border-radius: 20px;
          overflow: hidden;

          > img {
            width: 100%;
          }
        }
  
        .no {
          font-size: 1rem;
          color: #2478d7;
          margin-top: .5rem;
        }
  
        .desc {
          font-size: 1.2rem;
          color: #fff;
        }
      }

    }
  }
}

