.section-3 {
  margin-top: 10rem;

  > .title {
    font-size: 2.5rem;
    color: #fff;
    margin-bottom: 3rem;
    font-weight: 500;
  }

  .left-box {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .text {
      position: absolute;
      text-align: center;
      top: 40%;
      transform: translateY(-40%);

      > span:nth-of-type(1) {
        margin-bottom: 1rem;
        color: #fac001;
        font-size: 1.2rem;
        display: block;
      }

      > span:nth-of-type(2) {
        color: #fff;
        font-size: 1rem;
      }
    }

    img {
      max-width: 100%;
    }
  }

  .vision-list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .vision-item {
      display: flex;
      align-items: center;
      margin-bottom: 20px;

      .icon {
        background-color: #2f5597;
        border-radius: 10px;
        width: 80px;
        height: 80px;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .desc {
        margin-left: 60px;

        > li {
          color: #fff;
          text-indent: 3px;

          &:nth-of-type(1) {
            list-style: initial;
            text-indent: 0;
          }
        }
      }
    }
  }
}

.section-4 {
  margin-top: 10rem;

  > .title {
    font-size: 2.5rem;
    color: #fff;
    margin-bottom: 3rem;
    font-weight: 500;
  }

  .box-list {
    .box {
      background-color: #141e2f;
      border-radius: 30px;
      width: 250px;
      height: 250px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      .icon {
        color: #fff;
      }
      .title {
        font-size: 1rem;
        color: #2478d7;
      }
      .desc {
        font-size: 1.2rem;
        color: #fff;
      }
    }
  }
}
